div>\n</template>\n<script>\nimport { commonMixin } from '@/utils/mixins/commonMixin'\n\nexport default {\n    name: 'EquipmentDesc',\n    mixins: [commonMixin],\n    props: {\n        equipment: {\n            type: Object,\n            required: true\n        },\n        image: {\n            type: Boolean,\n            default: true\n        },\n        lockType: {\n            type: Array,\n            default: () => []\n        },\n        isBinding: {\n            type: Boolean,\n            default: false\n        }\n    },\n  methods: {\n    parseEquipDesc(desc, default_style = '#Y') {\n      if (!desc) return ''\n      if (typeof window.parse_style_info === 'function') {\n        return window.parse_style_info(desc, default_style)\n      }\n      return desc\n    },\n  }\n}\n</script>\n<style scoped>\n.icon-binding {\n  position: absolute;\n  right: -9px;\n  top: -9px;\n  color: red;\n  border: 3px solid red;\n  border-radius: 50%;\n  width: 40px;\n  height: 40px;\n  line-height: 40px;\n  font-weight: bold !important;\n  font-size: 16px;\n  transform: rotate(45deg);\n  background-color: rgb(245 108 108 / 20%);\n  user-select: none;\n}\n</style>"],"sourceRoot":""}]);
