   style=\"width: 100%\"\n    sortable\n    :sort-by=\"['price', 'similarity']\"\n    :sort-order=\"['ascending', 'descending']\"\n  >\n    <el-table-column fixed prop=\"price\" label=\" ()\" width=\"100\" sortable>\n      <template #default=\"scope\">\n        <span v-html=\"formatPrice(scope.row.price)\"></span>\n      </template>\n    </el-table-column>\n\n    <el-table-column prop=\"similarity\" label=\"\" width=\"80\" sortable  align=\"center\">\n      <template #default=\"scope\">\n        <el-tag :type=\"getSimilarityTagType(scope.row.similarity)\">\n          {{ scope.row.similarity.toFixed(3) }}\n        </el-tag>\n      </template>\n    </el-table-column>\n\n    <el-table-column prop=\"nickname\" label=\"\" width=\"60\" align=\"center\">\n      <template #default=\"scope\">\n       <RoleImage :roleInfo=\"scope.row.roleInfo\" :other_info=\"scope.row.other_info\" />\n      </template>\n    </el-table-column>\n    <el-table-column prop=\"server_name\" label=\"\" width=\"90\">\n      <template #default=\"scope\">\n        <div class=\"server-info\">\n          <div>  <el-tag type=\"danger\" size=\"mini\">{{ getServerHeatLabel(scope.row.serverid)||'' }}</el-tag></div>\n          <div> <el-tag type=\"danger\" size=\"mini\">{{ scope.row.server_name }}</el-tag></div>\n          <div v-if=\"scope.row.is_cross_server\" class=\"cross-server-tag\">\n            <el-tag size=\"mini\" type=\"info\"></el-tag>\n          </div>\n        </div>\n      </template>\n    </el-table-column>\n    <el-table-column prop=\"school_desc\" label=\"/\" width=\"80\" align=\"center\">\n      <template #default=\"scope\">\n        <el-tag size=\"mini\">\n          {{ scope.row.level }}\n        </el-tag>\n        <el-tag type=\"success\" size=\"mini\">\n          {{ scope.row.roleInfo.basic_info.school }}\n        </el-tag>\n      </template>\n    </el-table-column>\n    <el-table-column label=\"\" min-width=\"70\">\n      <template #default=\"scope\">\n        <div class=\"cultivation-info\">\n          <div class=\"cultivation-row\">\n            <span class=\"cultivation-values\">\n              {{ scope.row.features.expt_ski1 || 0 }}/{{ scope.row.features.expt_ski2 || 0 }}/{{ scope.row.features.expt_ski3 || 0 }}/{{ scope.row.features.expt_ski4 || 0 }}\n            </span>\n          </div>\n          <div class=\"cultivation-row\">\n            <span class=\"cultivation-values\">\n              {{ scope.row.features.beast_ski1 || 0 }}/{{ scope.row.features.beast_ski2 || 0 }}/{{ scope.row.features.beast_ski3 || 0 }}/{{ scope.row.features.beast_ski4 || 0 }}\n            </span>\n          </div>\n          <div class=\"cultivation-row\" v-if=\"scope.row.total_cultivation\">\n            <span class=\"cultivation-label\">:</span>\n            <span class=\"cultivation-values total-cultivation\">{{ scope.row.total_cultivation }}</span>\n          </div>\n        </div>\n      </template>\n    </el-table-column>\n\n    <el-table-column label=\"\" min-width=\"120\">\n      <template #default=\"scope\">\n        <div class=\"special-attributes\">\n          <div v-if=\"scope.row.sum_exp\" class=\"attr-item\">\n            <span class=\"attr-label\">:</span>\n            <span class=\"attr-value\">{{ scope.row.sum_exp }}</span>\n          </div>\n          <div v-if=\"scope.row.all_new_point\" class=\"attr-item\">\n            <span class=\"attr-label\">:</span>\n            <span class=\"attr-value special-value\">{{ scope.row.all_new_point }}</span>\n          </div>\n          <div v-if=\"scope.row.three_fly_lv\" class=\"attr-item\">\n            <span class=\"attr-label\">:</span>\n            <span class=\"attr-value special-value\">{{ scope.row.three_fly_lv }}</span>\n          </div>\n          <div v-if=\"scope.row.school_history_count > 1\" class=\"attr-item\">\n            <span class=\"attr-label\">:</span>\n            <span class=\"attr-value\">{{ scope.row.school_history_count }}</span>\n          </div>\n        </div>\n      </template>\n    </el-table-column>\n    <el-table-column label=\"\" width=\"80\" fixed=\"right\">\n      <template #default=\"scope\">\n        <el-link :href=\"getCBGLinkByType(scope.row.eid)\" type=\"danger\" target=\"_blank\"></el-link>\n      </template>\n    </el-table-column>\n  </el-table>\n</template>\n\n<script>\nimport { commonMixin } from '@/utils/mixins/commonMixin'\nimport RoleImage from './RoleInfo/RoleImage.vue'\nexport default {\n  name: 'SimilarRoleTable',\n  components: {\n    RoleImage\n  },\n  mixins: [commonMixin],\n  props: {\n    anchors: {\n      type: Array,\n      required: true\n    }\n  },\n  methods: {\n    getServerHeatLabel(serverid){\n      const serverHeat = window.hotServersConfig?.find(item => item.children.find(child => child.server_id === serverid))\n      return serverHeat?.server_name||''\n    },\n    getSimilarityTagType(similarity) {\n      if (similarity >= 0.9) {\n        return 'success'\n      } else if (similarity >= 0.7) {\n        return 'primary'\n      } else if (similarity >= 0.5) {\n        return 'info'\n      } else if (similarity >= 0.3) {\n        return 'warning'\n      } else {\n        return 'danger'\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n.price-cell {\n  font-weight: 600;\n  color: #e6a23c;\n}\n\n.role-name {\n  font-weight: 500;\n  color: #303133;\n}\n\n.cultivation-info {\n  font-size: 12px;\n}\n\n.cultivation-row {\n  display: flex;\n  align-items: center;\n  margin-bottom: 4px;\n}\n\n.cultivation-label {\n  color: #909399;\n  min-width: 60px;\n  font-size: 11px;\n}\n\n.cultivation-values {\n  color: #606266;\n  font-family: monospace;\n}\n\n.total-cultivation {\n  color: #409eff;\n  font-weight: 600;\n}\n\n.special-attributes {\n  font-size: 12px;\n}\n\n.attr-item {\n  display: flex;\n  align-items: center;\n  margin-bottom: 3px;\n}\n\n.attr-label {\n  color: #909399;\n  min-width: 50px;\n  font-size: 11px;\n}\n\n.attr-value {\n  color: #606266;\n}\n\n.special-value {\n  color: #e6a23c;\n  font-weight: 600;\n}\n\n.server-info {\n  text-align: center;\n}\n\n.cross-server-tag {\n  margin-top: 4px;\n}\n\n/*  */\n.el-table tbody tr:hover > td {\n  background-color: #f5f7fa !important;\n}\n\n/*  */\n.el-tag {\n  font-weight: 500;\n}\n</style> "],"sourceRoot":""}]);
