___CSS_LOADER_EXPORT___.push([module.id, "\n.special-info[data-v-6326d1fa] {\n  font-size: 12px;\n  color: #409eff;\n}\n.pet-skills[data-v-6326d1fa] {\n  margin-bottom: 2px;\n}\n.skills-text[data-v-6326d1fa] {\n  color: #3498db;\n}\n.pet-texing[data-v-6326d1fa] {\n  margin-top: 2px;\n}\n.texing-text[data-v-6326d1fa] {\n  color: #e67e22;\n}\n.cbg-link[data-v-6326d1fa] {\n  color: #409eff;\n  padding: 0;\n}\n.cbg-link[data-v-6326d1fa]:hover {\n  color: #66b1ff;\n}\n", "",{"version":3,"sources":["webpack://./src/components/SimilarPetTable.vue"],"names":[],"mappings":";AAkIA;EACA,eAAA;EACA,cAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,cAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,cAAA;AACA;AAEA;EACA,cAAA;EACA,UAAA;AACA;AAEA;EACA,cAAA;AACA","sourcesContent":["<template>\n  <el-table :data=\"anchors\" stripe max-height=\"300\" style=\"width: 100%\" sortable :sort-by=\"['price', 'similarity']\"\n    :sort-order=\"['ascending', 'descending']\">\n    <el-table-column fixed prop=\"price\" label=\" ()\" width=\"140\" sortable>\n      <template #default=\"scope\">\n        <span>{{ scope.row.server_name }}</span>\n        <div v-html=\"formatFullPrice(scope.row)\"></div>\n      </template>\n    </el-table-column>\n    <el-table-column fixed label=\"\" width=\"70\">\n      <template #default=\"scope\">\n        <pet-image :pet=\"scope.row.petData\" :equipFaceImg=\"scope.row.equip_face_img\" trigger=\"hover\" />\n      </template>\n    </el-table-column>\n    <el-table-column prop=\"similarity\" label=\"\" width=\"80\" sortable>\n      <template #default=\"scope\">\n        <el-tag :type=\"getSimilarityTagType(scope.row.similarity)\">\n          {{ scope.row.similarity }}\n        </el-tag>\n      </template>\n    </el-table-column>\n    <el-table-column prop=\"level\" label=\"\" width=\"140\">\n      <template #default=\"scope\">\n          <p :class=\"scope.row.petData.is_baobao === '' ? 'cBlue' : 'equip_desc_red'\">\n            <span>{{ scope.row.petData.is_baobao === '' ? '' : '' }}</span>\n            <span>{{ scope.row.equip_name }}{{ scope.row.petData.is_baobao === '' ? '' : '' }}/{{ scope.row.level\n            }}</span>\n          </p>\n          <p>{{ scope.row.role_grade_limit }}</p>\n        </template>\n    </el-table-column>\n    <el-table-column prop=\"growth\" label=\"\" width=\"60\">\n      <template #default=\"scope\">\n        <span v-html=\"getColorNumber(scope.row.growth, [1, 1.3])\"></span>\n      </template>\n    </el-table-column>\n    <el-table-column prop=\"lx\" label=\"\" width=\"60\"></el-table-column>\n    <el-table-column prop=\"equip_list\" label=\"\" width=\"80\">\n      <template #default=\"{ row: { equip_list } }\">\n        {{ getEquipSuitEffect(equip_list) }}\n      </template>\n    </el-table-column>\n    <el-table-column label=\"/\" width=\"120\">\n      <template #default=\"scope\">\n        <div class=\"special-info\">\n          <div class=\"pet-skills\">\n            <span v-if=\"scope.row.all_skill\" class=\"skills-text\">\n              {{ formatSkillsDiff(scope.row.all_skill,targetPet)}}\n            </span>\n          </div>\n          <div v-if=\"scope.row.texing\" class=\"pet-texing\">\n            <span class=\"texing-text\">\n              {{ formatTexing(scope.row.texing) }}\n            </span>\n          </div>\n        </div>\n      </template>\n    </el-table-column>\n    <el-table-column label=\"\" width=\"80\">\n      <template #default=\"scope\">\n        <el-link :href=\"getCBGLinkByType(scope.row.eid, 'pet')\" type=\"danger\" target=\"_blank\"></el-link>\n      </template>\n    </el-table-column>\n  </el-table>\n</template>\n\n<script>\nimport PetImage from './PetImage.vue'\nimport { petMixin } from '@/utils/mixins/petMixin'\nimport { equipmentMixin } from '@/utils/mixins/equipmentMixin'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\n\nexport default {\n  name: 'SimilarPetTable',\n  components: {\n    PetImage\n  },\n  mixins: [equipmentMixin, commonMixin, petMixin],\n  props: {\n    anchors: {\n      type: Array,\n      default: () => []\n    },\n    targetPet: {\n      type: Object,\n      default: null\n    }\n  },\n  methods: {\n    getEquipSuitEffect(equipList) {\n      if (!equipList) return ''\n\n      try {\n        const equipArray = JSON.parse(equipList).filter((equip) => equip)\n\n        // \n        const suitEffects = {}\n\n        // \n        equipArray.forEach((equip) => {\n          if (equip.desc) {\n            //  + \n            const suitMatch = equip.desc.match(/#c4DBAF4#c4DBAF4([^#]+)/)\n            if (suitMatch && suitMatch[1]) {\n              const suitName = suitMatch[1].trim()\n              suitEffects[suitName] = (suitEffects[suitName] || 0) + 1\n            }\n          }\n        })\n\n        // 3\n        for (const [suitName, count] of Object.entries(suitEffects)) {\n          if (count >= 3) {\n            return suitName\n          }\n          // 3\n          return suitName + `(${count}/3)`\n        }\n\n        return ''\n      } catch (error) {\n        console.error(':', error)\n        return ''\n      }\n    },\n  }\n}\n</script>\n\n<style scoped>\n.special-info {\n  font-size: 12px;\n  color: #409eff;\n}\n\n.pet-skills {\n  margin-bottom: 2px;\n}\n\n.skills-text {\n  color: #3498db;\n}\n\n.pet-texing {\n  margin-top: 2px;\n}\n\n.texing-text {\n  color: #e67e22;\n}\n\n.cbg-link {\n  color: #409eff;\n  padding: 0;\n}\n\n.cbg-link:hover {\n  color: #66b1ff;\n}\n</style>\n"],"sourceRoot":""}]);
