  border: 1px solid #f0f0f0;\n  border-radius: 8px;\n  margin-bottom: 10px;\n}\n.skeleton-header[data-v-3e1e2d34] {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 15px;\n}\n.skeleton-content[data-v-3e1e2d34] {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n.skeleton-price[data-v-3e1e2d34] {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-end;\n  gap: 8px;\n}\n.equip-tag-info[data-v-3e1e2d34] {\n  margin-top: 5px;\n}\n.equip-tag-info>*[data-v-3e1e2d34] {\n  margin-right: 5px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/EquipBatchValuationResult.vue"],"names":[],"mappings":";AAoRA;EACA,YAAA;EACA,cAAA;AACA;AAEA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,6BAAA;EACA,6DAAA;EACA,YAAA;EACA,kBAAA;EACA,mBAAA;EACA,yCAAA;AACA;AAEA;EACA,kBAAA;EACA,OAAA;AACA;AAEA;EACA,iBAAA;EACA,kBAAA;EACA,yCAAA;EACA,gBAAA;AACA;AAEA;EACA,aAAA;EACA,gCAAA;AACA;AAEA;EACA,SAAA;EACA,cAAA;AACA;AAEA;EACA,iBAAA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;EACA,gCAAA;EACA,iCAAA;EACA,iBAAA;AACA;AAEA;EACA,yBAAA;AACA;;AAEA,eAAA;AACA;EACA,yCAAA;AACA;AAEA;EACA,yCAAA;AACA;AAEA;EACA,0CAAA;AACA;AAEA;EACA,yCAAA;AACA;AAEA;EACA,0CAAA;AACA;AAEA;EACA,8BAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;;AAEA,eAAA;AACA;EACA,8BAAA;EACA,cAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,eAAA;AACA;AAGA;EACA,aAAA;EACA,mBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,iBAAA;EACA,cAAA;AACA;AAEA;EACA,cAAA;EACA,gBAAA;AACA;AAEA;EACA,cAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,gBAAA;EACA,kBAAA;AACA;;AAEA,UAAA;AACA;EACA,UAAA;AACA;AAEA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,mBAAA;EACA,kBAAA;AACA;AAEA;EACA,mBAAA;AACA;;AAEA,UAAA;AACA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,yBAAA;EACA,kBAAA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,sBAAA;EACA,qBAAA;EACA,QAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,iBAAA;AACA","sourcesContent":["<template>\n  <div class=\"batch-valuation-result\" :style=\"$attrs.style\">\n    <!-- Loading -->\n    <div v-if=\"loading\" class=\"skeleton-container\">\n      <el-skeleton :rows=\"12\" animated />\n    </div>\n    <!--  -->\n    <!--  -->\n    <template v-else>\n      <div class=\"result-item\" :class=\"getOverviewClass()\" style=\"min-height: unset;\">\n        <el-row :gutter=\"20\" style=\"margin-bottom: 20px;\">\n          <el-col :span=\"6\">\n            <el-statistic group-separator=\",\" :precision=\"2\" :value=\"currentTotalValue / 100\" title=\"\"\n              :value-style=\"{ fontSize: '28px', fontWeight: 'bold' }\">\n              <template slot=\"formatter\">\n                <span v-html=\"formatPrice(currentTotalValue)\"></span>\n              </template>\n            </el-statistic>\n          </el-col>\n          <el-col :span=\"6\">\n            <el-statistic group-separator=\",\" :precision=\"0\" :value=\"successCount\" title=\"\"\n              :value-style=\"{ fontSize: '28px', fontWeight: 'bold', color: '#67c23a' }\">\n              <template slot=\"suffix\">\n                <span style=\"color: #909399; font-size: 16px\">/ {{ totalCount }}</span>\n              </template>\n            </el-statistic>\n          </el-col>\n        </el-row>\n      </div>\n\n      <el-row type=\"flex\" style=\"flex-wrap: wrap;\">\n        <el-col :span=\"colSpan\" v-for=\"(item, index) in currentList\" :key=\"item.equip_sn || index\" class=\"result-item\"\n          :class=\"getResultItemClass(item)\">\n          <el-row type=\"flex\" align=\"middle\" justify=\"space-between\">\n            <el-col style=\"width: 50px;\">\n              <EquipmentImage placement=\"top\" :image=\"false\" :equipment=\"getEquipImageProps(item)\"\n                :lock-type=\"item.lock_type\" size=\"small\" :popoverWidth=\"300\" />\n            </el-col>\n            <el-col class=\"price-info\" style=\"width: calc(100% - 50px);\">\n              <el-statistic :value-style=\"getPriceStyle(item.confidence)\"> <template slot=\"formatter\">\n                  <span v-html=\"formatPrice(item.estimated_price)\"></span>\n                </template>\n              </el-statistic>\n            </el-col>\n          </el-row>\n          <div class=\"result-footer\">\n            <SimilarEquipmentModal :key=\"item.equip_sn\" :equipment=\"item\" :similar-data=\"similarData\" \n              @valuation-updated=\"(data) => handleValuationUpdated(data, item.resultIndex)\">\n              <el-link href=\"javascript:void(0)\" type=\"primary\" style=\"font-weight: bold;\">{{ item.name || ` ${index +\n                1}`\n              }}</el-link>\n            </SimilarEquipmentModal>\n            <el-tag :type=\"getConfidenceTagType(item.confidence)\" v-if=\"!item.error\">\n              : {{ (item.confidence * 100).toFixed(1) }}%\n            </el-tag>\n            <el-tag v-else type=\"danger\" :title=\"item.error\"></el-tag>\n          </div>\n          <div class=\"equip-tag-info\">\n            <el-tag type=\"success\" v-if=\"getEquipGemInfoAndBlueBlock(item.cDesc).gemLevel\">{{\n              getEquipGemInfoAndBlueBlock(item.cDesc).gemLevel }}</el-tag>\n            <el-tag v-for=\"tag in getEquipGemInfoAndBlueBlock(item.cDesc).blueBlock\" :key=\"tag\" type=\"primary\">{{ tag\n            }}</el-tag>\n          </div>\n        </el-col>\n      </el-row>\n    </template>\n    <!--  -->\n    <div class=\"dialog-footer\" style=\"text-align: center; margin-top: 20px;\">\n      <el-button @click=\"$emit('close')\" type=\"primary\"></el-button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport EquipmentImage from '@/components/EquipmentImage/EquipmentImage.vue'\nimport SimilarEquipmentModal from '@/components/SimilarEquipmentModal.vue'\nimport { equipmentMixin } from '@/utils/mixins/equipmentMixin'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\n\nexport default {\n  name: 'EquipBatchValuationResult',\n  props: {\n    results: {\n      type: Array,\n      required: true\n    },\n    equipmentList: {\n      type: Array,\n      required: true\n    },\n    totalValue: {\n      type: Number,\n      default: 0\n    },\n    valuateParams: {\n      type: Object,\n      required: true\n    },\n    loading: {\n      type: Boolean,\n      default: false\n    },\n    colSpan: {\n      type: Number,\n      default: 6\n    }\n  },\n  data() {\n    return {\n      similarData: null,\n      currentTotalValue: 0,\n      currentList: []\n    }\n  },\n  mixins: [equipmentMixin, commonMixin],\n  components: {\n    EquipmentImage,\n    SimilarEquipmentModal\n  },\n  watch: {\n    totalValue(newVal) {\n      this.currentTotalValue = newVal\n    },\n    results(newVal) {\n      //  results  currentList\n      if (newVal && newVal.length > 0) {\n        this.initializeCurrentList()\n      }\n    }\n  },\n  computed: {\n    totalCount() {\n      return this.results.length\n    },\n    successCount() {\n      return this.results.filter((result) => !result.error).length\n    }\n  },\n  mounted() {\n    this.currentTotalValue = this.totalValue\n    \n    //  currentList\n    if (this.results && this.results.length > 0) {\n      this.initializeCurrentList()\n    }\n  },\n  methods: {\n    //  currentList\n    initializeCurrentList() {\n      // \n      const resultsWithEquipment = this.results.map((result, index) => ({\n        ...this.equipmentList[index],  // \n        ...result,  // \n        resultIndex: index\n      }))\n\n      // \n      const highConfidenceResults = resultsWithEquipment.filter(result => result.confidence === 1)\n      const lowConfidenceResults = resultsWithEquipment.filter(result => result.confidence !== 1)\n\n      // \n      highConfidenceResults.sort((a, b) => a.estimated_price - b.estimated_price)\n      lowConfidenceResults.sort((a, b) => a.confidence - b.confidence)\n\n      // \n      this.currentList = [...lowConfidenceResults, ...highConfidenceResults]\n    },\n    \n    // \n    handleValuationUpdated(data, resultIndex) {\n      console.log(', resultIndex:', resultIndex, 'data:', data)\n      \n      // \n      if (resultIndex === undefined || resultIndex < 0 || resultIndex >= this.results.length) {\n        console.error('resultIndex:', resultIndex)\n        return\n      }\n      \n      const oldResult = this.results[resultIndex]\n      if (!oldResult) {\n        console.error(', resultIndex:', resultIndex)\n        return\n      }\n      \n      // estimated_priceestimated_price\n      const oldPrice = oldResult.estimated_price || 0\n      const newPrice = data.estimated_price || 0\n      this.currentTotalValue = this.currentTotalValue - oldPrice + newPrice\n      \n      // \n      // data confidence, estimated_price, anchor_count\n      //  data watch  equipmentList \n      this.$set(this.results, resultIndex, {\n        ...this.equipmentList[resultIndex],  // \n        ...data  // \n      })\n      \n      console.log(', :', data.confidence)\n      \n      // \n      this.$emit('valuation-updated', data, resultIndex)\n    },\n\n    // \n    getConfidenceTagType(confidence) {\n      if (confidence >= 0.9) {\n        return 'success'  //  - \n      } else if (confidence >= 0.7) {\n        return ''  //  - \n      } else if (confidence >= 0.5) {\n        return 'info'     //  - \n      } else if (confidence >= 0.3) {\n        return 'warning'     //  - \n      } else {\n        return 'danger'   //  - \n      }\n    },\n\n    // CSS\n    getResultItemClass(result) {\n      if (result.error) {\n        return 'error'\n      }\n\n      // \n      const confidence = result.confidence || 0\n      if (confidence >= 0.9) {\n        return 'confidence-high'\n      } else if (confidence >= 0.7) {\n        return 'confidence-medium'\n      } else if (confidence >= 0.5) {\n        return 'confidence-low'\n      } else if (confidence >= 0.3) {\n        return 'confidence-very-low'\n      } else {\n        return 'confidence-extremely-low'\n      }\n    },\n\n    // \n    getPriceStyle(confidence) {\n      const baseStyle = { fontSize: '18px', fontWeight: 'bold', justifyContent: 'end' }\n\n      if (!confidence || confidence < 0.3) {\n        return { ...baseStyle, color: '#f56c6c' }  //  - \n      } else if (confidence < 0.5) {\n        return { ...baseStyle, color: '#e6a23c' }  //  - \n      } else if (confidence < 0.7) {\n        return { ...baseStyle, color: '#909399' }  //  - \n      } else if (confidence < 0.9) {\n        return { ...baseStyle, color: '#409eff' }  //  - \n      } else {\n        return { ...baseStyle, color: '#67c23a' }  //  - \n      }\n    },\n\n    // \n    getOverviewClass() {\n      const successRate = this.totalCount > 0 ? (this.successCount / this.totalCount) : 0\n      if (successRate >= 0.9) {\n        return 'confidence-high'\n      } else if (successRate >= 0.7) {\n        return 'confidence-medium'\n      } else if (successRate >= 0.5) {\n        return 'confidence-low'\n      } else if (successRate >= 0.3) {\n        return 'confidence-very-low'\n      } else {\n        return 'confidence-extremely-low'\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n.batch-valuation-result {\n  width: 960px;\n  margin: 0 auto;\n}\n\n.result-overview {\n  margin-bottom: 30px;\n}\n\n.overview-card {\n  display: flex;\n  justify-content: space-around;\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n  color: white;\n  padding: 30px 20px;\n  border-radius: 12px;\n  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);\n}\n\n.overview-item {\n  text-align: center;\n  flex: 1;\n}\n\n.result-details {\n  background: white;\n  border-radius: 8px;\n  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);\n  overflow: hidden;\n}\n\n.details-header {\n  padding: 20px;\n  border-bottom: 1px solid #ebeef5;\n}\n\n.details-header h3 {\n  margin: 0;\n  color: #303133;\n}\n\n.results-list {\n  max-height: 400px;\n  overflow-y: auto;\n}\n\n.result-item {\n  padding: 15px 20px;\n  border-bottom: 1px solid #f0f0f0;\n  transition: background-color 0.3s;\n  min-height: 100px;\n}\n\n.result-item:hover {\n  background-color: #f5f7fa;\n}\n\n/*  */\n.result-item.confidence-high:hover {\n  background-color: rgba(103, 194, 58, 0.1);\n}\n\n.result-item.confidence-medium:hover {\n  background-color: rgba(64, 158, 255, 0.1);\n}\n\n.result-item.confidence-low:hover {\n  background-color: rgba(144, 147, 153, 0.1);\n}\n\n.result-item.confidence-very-low:hover {\n  background-color: rgba(230, 162, 60, 0.1);\n}\n\n.result-item.confidence-extremely-low:hover {\n  background-color: rgba(245, 108, 108, 0.1);\n}\n\n.result-item.success {\n  border-left: 4px solid #67c23a;\n}\n\n.result-item.error {\n  border-left: 4px solid #f56c6c;\n  /*  -  */\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n\n/*  */\n.result-item.confidence-high {\n  border-left: 4px solid #67c23a;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f9ff 0%, #e1f3d8 100%);\n}\n\n.result-item.confidence-medium {\n  border-left: 4px solid #409eff;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n\n.result-item.confidence-low {\n  border-left: 4px solid #909399;\n  /*  -  */\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n\n.result-item.confidence-very-low {\n  border-left: 4px solid #e6a23c;\n  /*  -  */\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n\n.result-item.confidence-extremely-low {\n  border-left: 4px solid #f56c6c;\n  /*  -  */\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n\n.result-footer {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-top: 8px;\n}\n\n\n.price-info {\n  display: flex;\n  align-items: center;\n}\n\n:global(.price-info .el-statistic .con) {\n  justify-content: flex-end;\n}\n\n.equipment-info {\n  text-align: right;\n}\n\n.equip-name {\n  font-weight: bold;\n  color: #303133;\n}\n\n.equip-level {\n  color: #909399;\n  margin-left: 5px;\n}\n\n.error-content {\n  color: #f56c6c;\n}\n\n.error-message {\n  font-size: 14px;\n}\n\n.result-actions {\n  margin-top: 20px;\n  text-align: center;\n}\n\n/*  */\n.results-list::-webkit-scrollbar {\n  width: 6px;\n}\n\n.results-list::-webkit-scrollbar-track {\n  background: #f1f1f1;\n  border-radius: 3px;\n}\n\n.results-list::-webkit-scrollbar-thumb {\n  background: #c1c1c1;\n  border-radius: 3px;\n}\n\n.results-list::-webkit-scrollbar-thumb:hover {\n  background: #a8a8a8;\n}\n\n/*  */\n.skeleton-container {\n  padding: 20px 0;\n}\n\n.skeleton-item {\n  padding: 15px;\n  border: 1px solid #f0f0f0;\n  border-radius: 8px;\n  margin-bottom: 10px;\n}\n\n.skeleton-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 15px;\n}\n\n.skeleton-content {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.skeleton-price {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-end;\n  gap: 8px;\n}\n\n.equip-tag-info {\n  margin-top: 5px;\n}\n\n.equip-tag-info>* {\n  margin-right: 5px;\n}\n</style>\n"],"sourceRoot":""}]);
