___CSS_LOADER_EXPORT___.push([module.id, "\n.similar-header[data-v-5b020959] {\n  margin-bottom: 16px;\n  padding-bottom: 12px;\n  border-bottom: 1px solid #ebeef5;\n}\n.similar-header h4[data-v-5b020959] {\n  margin: 0 0 8px 0;\n  color: #303133;\n  font-size: 16px;\n}\n.similar-header p[data-v-5b020959] {\n  margin: 0 0 8px 0;\n  color: #606266;\n  font-size: 14px;\n}\n.stats[data-v-5b020959] {\n  display: flex;\n  gap: 20px;\n  font-size: 12px;\n  color: #909399;\n}\n.error-info[data-v-5b020959] {\n  padding: 20px 0;\n}\n.loading-info[data-v-5b020959] {\n  padding: 20px;\n}\n", "",{"version":3,"sources":["webpack://./src/components/SimilarPetModal.vue"],"names":[],"mappings":";AAyOA;EACA,mBAAA;EACA,oBAAA;EACA,gCAAA;AACA;AAEA;EACA,iBAAA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,iBAAA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;EACA,cAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,aAAA;AACA","sourcesContent":["<template>\n  <el-popover :data-equip-sn=\"pet.equip_sn\" :placement=\"placement\" width=\"860\" trigger=\"click\"\n    popper-class=\"similar-pet-popper\" v-model=\"visible\" @show=\"handleShow\">\n    <template #reference>\n      <slot>\n        <el-link type=\"primary\" style=\"font-size: 12px;\"></el-link>\n      </slot>\n    </template>\n\n    <!--  -->\n    <div v-if=\"visible\">\n      <!--  -->\n      <div v-if=\"valuationLoading\" class=\"loading-info\">\n        <el-skeleton :rows=\"12\" animated />\n      </div>\n      <div v-else-if=\"similarData\">\n        <div class=\"similar-header\">\n          <h4> ({{ similarData.anchor_count }}) <el-divider direction=\"vertical\" />\n            <el-tag type=\"info\" size=\"mini\">: {{ similarData.similarity_threshold }}</el-tag>\n            <el-divider direction=\"vertical\" />\n            <el-tag type=\"info\" size=\"mini\">: {{ similarData.max_anchors }}</el-tag>\n          </h4>\n          <!--  -->\n          <PetValuation :valuation=\"petValuation\" :target-pet=\"pet\" :equip_sn=\"pet.equip_sn\" @refresh=\"refresh\" />\n          <div v-if=\"similarData.statistics\" class=\"stats\">\n            <span>\n              :\n              <span v-html=\"formatPrice(similarData.statistics.price_range.min)\"></span>\n              -\n              <span v-html=\"formatPrice(similarData.statistics.price_range.max)\"></span>\n            </span>\n            <span> : {{ similarData.statistics.similarity_range.avg.toFixed(3) }} </span>\n          </div>\n        </div>\n\n        <!--  -->\n        <el-empty v-if=\"!anchorsLoading && !similarData?.anchors?.length\" description=\"\"></el-empty>\n        <SimilarPetTable v-else :anchors=\"similarData.anchors\" v-loading=\"anchorsLoading\" element-loading-text=\"\" :target-pet=\"pet\" />\n      </div>\n    </div>\n  </el-popover>\n</template>\n\n<script>\nimport PetValuation from './PetValuation.vue'\nimport SimilarPetTable from './SimilarPetTable.vue'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\nimport { petMixin } from '@/utils/mixins/petMixin'\nexport default {\n  name: 'SimilarPetModal',\n  components: {\n    PetValuation,\n    SimilarPetTable\n  },\n  mixins: [commonMixin, petMixin],\n  props: {\n    pet: {\n      type: Object,\n      required: true\n    },\n    placement: {\n      type: String,\n      default: 'left-end'\n    },\n    similarityThreshold: {\n      type: Number,\n      default: 0.8\n    },\n    maxAnchors: {\n      type: Number,\n      default: 30\n    },\n  },\n  data() {\n    return {\n      visible: false,\n      valuationLoading: false,  // \n      anchorsLoading: false,    // \n      error: false,\n      similarData: null,\n      petValuation: {}\n    }\n  },\n  computed: {\n    // \n    loading() {\n      return this.valuationLoading || this.anchorsLoading\n    }\n  },\n  mounted() {\n    // Chrome\n    this.$root.$on('search-task-completed', this.handleSearchCompleted)\n  },\n  beforeDestroy() {\n    // \n    this.$root.$off('search-task-completed', this.handleSearchCompleted)\n  },\n  methods: {\n    // \n    handleSearchCompleted() {\n      // \n      if (this.visible && this.similarData) {\n        console.log('')\n        this.refresh()\n      }\n    },\n    async handleShow() {\n      if (!this.similarData) {\n        await this.loadSimilarPets()\n      }\n    },\n\n    async refresh() {\n      await this.loadSimilarPets()\n    },\n\n    // \n    async loadSimilarPets() {\n      this.error = false\n      this.petValuation = {}\n      this.similarData = null\n\n      try {\n        await this.loadPetValuation(this.pet, this.similarityThreshold)\n      } catch (error) {\n        console.error(':', error)\n        this.error = true\n      }\n    },\n\n    // \n    async loadPetValuation({ petData, ...pet }, similarityThreshold) {\n      try {\n        // \n        this.valuationLoading = true\n        const response = await this.$api.pet.getPetValuation({\n          pet_data: pet,\n          strategy: 'fair_value',\n          similarity_threshold: similarityThreshold,\n          max_anchors: this.maxAnchors\n        })\n\n        const result = response.data\n        this.petValuation = result\n\n        // \n        this.$emit('valuation-updated', result)\n\n        // \n        this.similarData = {\n          anchor_count: result.anchor_count,\n          similarity_threshold: result.similarity_threshold || similarityThreshold,\n          max_anchors: result.max_anchors || this.maxAnchors,\n          anchors: [],\n          statistics: {\n            price_range: result.anchor_count?{\n              min: Math.min(...result.anchors.map((a) => a.price || 0)),\n              max: Math.max(...result.anchors.map((a) => a.price || 0)),\n              avg: result.anchors.reduce((sum, a) => sum + (a.price || 0), 0) / result.anchors.length\n            }:{\n              min:0,\n              max:0,\n              avg:0\n            },\n            similarity_range: result.anchor_count?{\n              min: Math.min(...result.anchors.map((a) => a.similarity || 0)),\n              max: Math.max(...result.anchors.map((a) => a.similarity || 0)),\n              avg: result.anchors.reduce((sum, a) => sum + (a.similarity || 0), 0) / result.anchors.length\n            }:{\n              min:0,\n              max:0,\n              avg:0\n            }\n          }\n        }\n\n        this.valuationLoading = false\n\n        // \n        if (result?.anchor_count > 0 && result?.anchors?.length > 0) {\n          // \n          this.anchorsLoading = true\n\n          try {\n            // equip_sn_list\n            const anchorsResponse = await this.$api.pet.getPetList({\n              page_size: 99,\n              equip_sn_list: result.anchors.map(item => item.equip_sn)\n            })\n\n            this.anchorsLoading = false\n            // \n            if (anchorsResponse.code === 200 && anchorsResponse.data?.data) {\n              const anchorsData = anchorsResponse.data.data\n              const parsedAnchors = anchorsData.map((item, index) => {\n                const petData = this.parsePetInfo(item.desc)\n                // \n                item.similarity = result.anchors[index].similarity\n                item.features = result.anchors[index].features\n                item.petData = petData\n                return item\n              })\n\n              // \n              this.$set(this.similarData, 'anchors', parsedAnchors)\n            } else {\n              console.warn(':', anchorsResponse.message)\n            }\n          } catch (error) {\n            console.error(':', error)\n            // \n          }\n        }\n\n      } catch (error) {\n        console.error(':', error)\n        this.$notify.error({\n          title: '',\n          message: '',\n          duration: 3000\n        })\n        throw error\n      } finally {\n        // \n        this.valuationLoading = false\n        this.anchorsLoading = false\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n.similar-header {\n  margin-bottom: 16px;\n  padding-bottom: 12px;\n  border-bottom: 1px solid #ebeef5;\n}\n\n.similar-header h4 {\n  margin: 0 0 8px 0;\n  color: #303133;\n  font-size: 16px;\n}\n\n.similar-header p {\n  margin: 0 0 8px 0;\n  color: #606266;\n  font-size: 14px;\n}\n\n.stats {\n  display: flex;\n  gap: 20px;\n  font-size: 12px;\n  color: #909399;\n}\n\n.error-info {\n  padding: 20px 0;\n}\n\n.loading-info {\n  padding: 20px;\n}\n</style>"],"sourceRoot":""}]);
