 in logs\" :key=\"index\" class=\"log-line\" :class=\"getLogLevel(log)\">\n            <span class=\"log-time\">{{ getLogTime(log) }}</span>\n            <span class=\"log-content\">{{ getLogContent(log) }}</span>\n          </div>\n        </div>\n      </div>\n    </div>\n  </el-card>\n</template>\n\n<script>\nexport default {\n  name: 'LogMonitor',\n  props: {\n    //\n    simpleMode: {\n      type: Boolean,\n      default: false\n    },\n    maxLines: {\n      type: Number,\n      default: 100\n    },\n    isRunning: {\n      type: Boolean,\n      default: false\n    }\n  },\n  data() {\n    return {\n      // \n      logsLoading: false,\n      // \n      logs: [],\n      logsInfo: null,\n      isLogStreaming: false,\n      logEventSource: null\n    }\n  },\n  watch: {\n    isRunning(newVal) {\n      if (newVal) {\n        this.startLogStream()\n      } else {\n        this.stopLogStream()\n      }\n    }\n  },\n  mounted() {\n    // \n    this.refreshLogs()\n  },\n  beforeDestroy() {\n    this.stopLogStream()\n  },\n  methods: {\n    // \n    async refreshLogs() {\n      this.logsLoading = true\n      try {\n        const params = {\n          lines: this.maxLines,\n          type: 'current'\n        }\n\n        const response = await this.$api.spider.getLogs(params)\n        if (response.code === 200) {\n          this.logs = response.data.logs || []\n          this.logsInfo = response.data\n          this.scrollToBottom()\n        } else {\n          this.$notify.error(response.message || '')\n        }\n      } catch (error) {\n        this.$notify.error(': ' + error.message)\n      } finally {\n        this.logsLoading = false\n      }\n    },\n\n    toggleLogStream() {\n      if (this.isLogStreaming) {\n        this.stopLogStream()\n      } else {\n        this.startLogStream()\n      }\n    },\n\n    startLogStream() {\n      if (this.isLogStreaming) return\n\n      try {\n        this.logEventSource = this.$api.spider.streamLogs()\n        this.isLogStreaming = true\n\n        this.logEventSource.onmessage = (event) => {\n          if (event.data) {\n            try {\n              // JSON\n              const data = JSON.parse(event.data)\n              if (data.log) {\n                this.logs.push(data.log)\n              } else if (typeof data === 'string') {\n                this.logs.push(data)\n              }\n            } catch (e) {\n              // JSON\n              this.logs.push(event.data)\n            }\n\n            // 100\n            if (this.logs.length > this.maxLines) {\n              this.logs = this.logs.slice(-this.maxLines)\n            }\n            this.scrollToBottom()\n          }\n        }\n\n        this.logEventSource.onerror = (error) => {\n          console.error(':', error)\n          // \n          setTimeout(() => {\n            if (this.isLogStreaming) {\n              this.stopLogStream()\n              this.startLogStream()\n            }\n          }, 5000)\n        }\n\n        this.logEventSource.onopen = () => {\n          console.log('')\n        }\n\n        // \n        if (this.logs.length === 0) {\n          this.logs.push('...')\n        }\n      } catch (error) {\n        console.error(':', error)\n        // \n      }\n    },\n\n    stopLogStream() {\n      if (this.logEventSource) {\n        try {\n          this.logEventSource.close()\n        } catch (e) {\n          console.log(':', e)\n        }\n        this.logEventSource = null\n      }\n      this.isLogStreaming = false\n    },\n\n    clearLogs() {\n      this.logs = []\n      this.logsInfo = null\n    },\n\n    scrollToBottom() {\n      this.$nextTick(() => {\n        const container = this.$refs.logsContainer\n        if (container) {\n          container.scrollTop = container.scrollHeight\n        }\n      })\n    },\n\n    getLogLevel(log) {\n      if (log.includes('ERROR') || log.includes('')) return 'log-error'\n      if (log.includes('WARNING') || log.includes('')) return 'log-warning'\n      if (log.includes('INFO') || log.includes('')) return 'log-info'\n      return 'log-default'\n    },\n\n    getLogTime(log) {\n      // \n      const timeMatch = log.match(/(\\d{4}-\\d{2}-\\d{2} \\d{2}:\\d{2}:\\d{2})/)\n      return timeMatch ? timeMatch[1] : ''\n    },\n\n    getLogContent(log) {\n      // \n      return log.replace(/^\\d{4}-\\d{2}-\\d{2} \\d{2}:\\d{2}:\\d{2}[,\\d]*\\s*/, '')\n    }\n  }\n}\n</script>\n\n<style scoped>\n/*  */\n.logs-card {\n  margin-bottom: 20px;\n  &.simple-mode {\n    margin-bottom: 0;\n  }\n}\n.logs-card.simple-mode :deep(.el-card__body)  {\n    padding: 0 !important;\n}\n\n.logs-card.simple-mode .logs-content,.logs-card.simple-mode .logs-content .logs-container  {\n    height: 200px;\n    padding: 0 !important;\n}\n.card-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  font-weight: bold;\n  font-size: 16px;\n}\n\n/*  */\n.logs-content {\n  padding: 10px 0;\n}\n\n.logs-container {\n  height: 400px;\n  overflow-y: auto;\n  background-color: #1e1e1e;\n  border-radius: 6px;\n  padding: 15px;\n  font-family: 'Consolas', 'Monaco', 'Courier New', monospace;\n  font-size: 13px;\n  line-height: 1.4;\n}\n\n.no-logs {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  height: 100%;\n  color: #666;\n}\n\n.no-logs i {\n  font-size: 48px;\n  margin-bottom: 10px;\n}\n\n.log-lines {\n  display: flex;\n  flex-direction: column;\n}\n\n.log-line {\n  padding: 2px 0;\n  display: flex;\n  align-items: flex-start;\n  word-break: break-all;\n}\n\n.log-time {\n  font-size: 12px;\n  color: #888;\n  min-width: 130px;\n  flex-shrink: 0;\n}\n\n.log-content {\n  color: #e2e8f0;\n  flex: 1;\n  font-size: 12px;\n}\n\n.log-error {\n  background-color: rgba(245, 108, 108, 0.1);\n  border-left: 3px solid #f56c6c;\n  padding-left: 10px;\n}\n\n.log-error .log-content {\n  color: #f56c6c;\n}\n\n.log-warning {\n  background-color: rgba(230, 162, 60, 0.1);\n  border-left: 3px solid #e6a23c;\n  padding-left: 10px;\n}\n\n.log-warning .log-content {\n  color: #e6a23c;\n}\n\n.log-info {\n  background-color: rgba(64, 158, 255, 0.1);\n  border-left: 3px solid #409eff;\n  padding-left: 10px;\n}\n\n.log-info .log-content {\n  color: #409eff;\n}\n\n.log-default .log-content {\n  color: #e2e8f0;\n}\n</style> "],"sourceRoot":""}]);
