deg, #f0f9ff 0%, #e1f3d8 100%);\n}\n.valuation-info.confidence-medium[data-v-78818f48] {\n  border-left: 4px solid #409eff;\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n.valuation-info.confidence-low[data-v-78818f48] {\n  border-left: 4px solid #909399;\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n.valuation-info.confidence-very-low[data-v-78818f48] {\n  border-left: 4px solid #e6a23c;\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n.valuation-info.confidence-extremely-low[data-v-78818f48] {\n  border-left: 4px solid #f56c6c;\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n", "",{"version":3,"sources":["webpack://./src/components/SimilarRoleModal.vue"],"names":[],"mappings":";AAwYA;EACA,oBAAA;EACA,gCAAA;AACA;AAEA;EACA,SAAA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,cAAA;EACA,aAAA;EACA,yBAAA;EACA,kBAAA;EACA,8BAAA;EACA,aAAA;EACA,sBAAA;EACA,QAAA;AACA;AAEA;EACA,eAAA;EACA,cAAA;AACA;AAEA;EACA,eAAA;EACA,aAAA;EACA,mBAAA;EACA,eAAA;EACA,QAAA;AACA;AAEA;EACA,cAAA;EACA,gBAAA;AACA;AAEA;EACA,cAAA;EACA,gBAAA;EACA,eAAA;AACA;AAEA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,QAAA;EACA,gBAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,eAAA;EACA,YAAA;AACA;AAEA;EACA,aAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;EACA,cAAA;EACA,eAAA;EACA,gBAAA;AACA;;AAEA,YAAA;AACA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;;AAEA,eAAA;AACA;EACA,8BAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,6DAAA;AACA","sourcesContent":["<template>\n  <el-popover :data-role-eid=\"role.eid\" :placement=\"placement\" width=\"800\" trigger=\"click\"\n    popper-class=\"similar-role-popper\" v-model=\"visible\" @show=\"handleShow\" :disabled=\"disabled\">\n    <template #reference>\n      <slot>\n        <el-link type=\"primary\" style=\"font-size: 12px;\"></el-link>\n      </slot>\n    </template>\n\n    <!--  -->\n    <div v-if=\"visible\">\n      <!--  -->\n      <div v-if=\"valuationLoading\" class=\"loading-info\">\n        <el-skeleton :rows=\"12\" animated />\n      </div>\n      <div v-else-if=\"similarData\">\n        <div class=\"similar-header\">\n          <h4> ({{ similarData.anchor_count }}) <el-divider direction=\"vertical\" />\n            <el-tag type=\"info\" size=\"mini\">: {{ similarData.similarity_threshold }}</el-tag>\n            <el-divider direction=\"vertical\" />\n            <el-tag type=\"info\" size=\"mini\">: {{ similarData.max_anchors }}</el-tag>\n          </h4>\n\n          <!--  -->\n          <div class=\"valuation-info\" :class=\"confidenceClass\">\n            <el-row type=\"flex\" align=\"middle\" justify=\"space-between\">\n              <el-row type=\"flex\" align=\"middle\">\n                <RoleImage :key=\"role.eid\" :other_info=\"role.other_info\" :roleInfo=\"role.roleInfo\" />\n                <div style=\"margin-left: 10px\">\n                  <div class=\"role-basic-info\">\n                    <el-tag type=\"danger\" size=\"mini\"> {{ getServerHeatLabel(role.serverid) || '' }}/{{\n                      role.server_name\n                      }}</el-tag>\n                    /\n                    <el-tag type=\"primary\" size=\"mini\">{{ role.level }}</el-tag>\n                    /\n                    <el-tag type=\"info\" size=\"mini\">{{ role.roleInfo.basic_info.school }}</el-tag>\n                  </div>\n                </div>\n              </el-row>\n              <!--  -->\n              <div style=\"width: 100px;flex-shrink: 0;display: flex;justify-content: flex-end;\">\n                <el-button type=\"primary\" @click=\"refresh\" size=\"mini\" :loading=\"loading\"></el-button>\n              </div>\n            </el-row>\n\n            <div class=\"valuation-main\">\n              <span class=\"valuation-label\">:</span>\n              <span class=\"valuation-price\">{{ similarData.valuation?.estimated_price_yuan || '-' }}</span>\n              <span class=\"valuation-strategy\">({{ getStrategyName(similarData.valuation?.strategy) }})</span>\n            </div>\n\n            <div class=\"valuation-details\">\n              <span class=\"confidence-display\" :class=\"confidenceTextClass\">\n                <i :class=\"confidenceIcon\"></i>\n                : {{ similarData.valuation ? (similarData.valuation.confidence * 100).toFixed(1) + '%' : '-' }}\n                <span class=\"confidence-level\">{{ confidenceLevel }}</span>\n              </span>\n              <span>{{ similarData.anchor_count}}</span>\n            </div>\n          </div>\n\n          <!--  -->\n          <div v-if=\"similarData.statistics\" class=\"stats\">\n            <span>\n              :\n              <span v-html=\"formatPrice(similarData.statistics.price_range.min)\"></span>\n              -\n              <span v-html=\"formatPrice(similarData.statistics.price_range.max)\"></span>\n            </span>\n            <span>: {{ similarData.statistics.similarity_range.avg.toFixed(3) }}</span>\n          </div>\n        </div>\n\n        <!--  -->\n        <el-empty v-if=\"!anchorsLoading && !similarData.anchors?.length\" description=\"\"></el-empty>\n        <SimilarRoleTable v-else :anchors=\"similarData.anchors\" v-loading=\"anchorsLoading\" element-loading-text=\"\" />\n      </div>\n    </div>\n  </el-popover>\n</template>\n\n<script>\nimport SimilarRoleTable from './SimilarRoleTable.vue'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\nimport RoleImage from './RoleInfo/RoleImage.vue'\n\nexport default {\n  name: 'SimilarRoleModal',\n  components: {\n    SimilarRoleTable,\n    RoleImage\n  },\n  mixins: [commonMixin],\n  props: {\n    disabled: {\n      type: Boolean,\n      default: false\n    },\n    role: {\n      type: Object,\n      required: true\n    },\n    placement: {\n      type: String,\n      default: 'left-end'\n    },\n    // \n    searchParams: {\n      type: Object,\n      default: () => ({\n        selectedDate: '2025-01',\n        roleType: 'empty'\n      })\n    }\n  },\n  data() {\n    return {\n      visible: false,\n      valuationLoading: false,  // \n      anchorsLoading: false,    // \n      error: false,\n      similarData: null,\n      hotServersConfig: window.hotServersConfig || []\n    }\n  },\n  computed: {\n    // \n    loading() {\n      return this.valuationLoading || this.anchorsLoading\n    },\n    confidenceClass() {\n      if (!this.similarData?.valuation?.confidence) {\n        return 'confidence-extremely-low'\n      }\n\n      const confidence = this.similarData.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return 'confidence-high'\n      } else if (confidence >= 0.6) {\n        return 'confidence-medium'\n      } else if (confidence >= 0.4) {\n        return 'confidence-low'\n      } else if (confidence >= 0.2) {\n        return 'confidence-very-low'\n      } else {\n        return 'confidence-extremely-low'\n      }\n    },\n    confidenceTextClass() {\n      if (!this.similarData?.valuation?.confidence) {\n        return 'text-danger'\n      }\n\n      const confidence = this.similarData.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return 'text-success'\n      } else if (confidence >= 0.6) {\n        return 'text-primary'\n      } else if (confidence >= 0.4) {\n        return 'text-info'\n      } else if (confidence >= 0.2) {\n        return 'text-warning'\n      } else {\n        return 'text-danger'\n      }\n    },\n    confidenceIcon() {\n      if (!this.similarData?.valuation?.confidence) {\n        return 'el-icon-warning'\n      }\n\n      const confidence = this.similarData.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return 'el-icon-success'\n      } else if (confidence >= 0.6) {\n        return 'el-icon-info'\n      } else if (confidence >= 0.4) {\n        return 'el-icon-question'\n      } else if (confidence >= 0.2) {\n        return 'el-icon-warning'\n      } else {\n        return 'el-icon-error'\n      }\n    },\n    confidenceLevel() {\n      if (!this.similarData?.valuation?.confidence) {\n        return '()'\n      }\n\n      const confidence = this.similarData.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return '()'\n      } else if (confidence >= 0.6) {\n        return '()'\n      } else if (confidence >= 0.4) {\n        return '()'\n      } else if (confidence >= 0.2) {\n        return '()'\n      } else {\n        return '()'\n      }\n    }\n  },\n  watch: {\n    visible: {\n      handler(newVal) {\n        if (newVal) {\n          this.loadHotServers()\n        }\n      },\n      immediate: true\n    }\n  },\n  mounted() {\n    // Chrome\n    this.$root.$on('search-task-completed', this.handleSearchCompleted)\n  },\n  beforeDestroy() {\n    // \n    this.$root.$off('search-task-completed', this.handleSearchCompleted)\n  },\n  methods: {\n    // \n    handleSearchCompleted() {\n      // \n      if (this.visible && this.similarData) {\n        console.log('')\n        this.refresh()\n      }\n    },\n    getServerHeatLabel(serverid) {\n      const serverHeat = this.hotServersConfig.find(item => item.children.find(child => child.server_id === serverid))\n      return serverHeat?.server_name || ''\n    },\n    async loadHotServers() {\n      if (!window.hotServersConfig) {\n        window.hotServersConfig = await this.$api.system.getHotServers()\n      }\n      this.hotServersConfig = window.hotServersConfig\n    },\n    async handleShow() {\n      // \n      this.$emit('show', this.role)\n      // \n      await this.loadSimilarRoles()\n    },\n    async refresh() {\n      await this.loadSimilarRoles()\n    },\n    // \n    async loadSimilarRoles() {\n      this.error = false\n      this.similarData = null\n\n      try {\n        await this.loadRoleValuation()\n      } catch (error) {\n        console.error(':', error)\n        this.error = true\n      }\n    },\n\n    // \n    async loadRoleValuation() {\n      try {\n        // \n        this.valuationLoading = true\n        const response = await this.$api.role.getRoleValuation({\n          eid: this.role.eid,\n          role_type: this.searchParams.roleType,\n          strategy: 'fair_value',\n          similarity_threshold: 0.7,\n          max_anchors: 30\n        })\n\n\n        const result = response.data\n        const currentAchors = result.anchors\n        const estimatedPrice = result.estimated_price_yuan\n\n        // \n        this.$emit('update-role-price', {\n          eid: this.role.eid,\n          basePrice: result.estimated_price\n        })\n\n        // \n        this.similarData = {\n          anchor_count: result.anchor_count,\n          similarity_threshold: result.similarity_threshold || 0.7,\n          max_anchors: result.max_anchors || 30,\n          anchors: [],\n          statistics: {\n            price_range:result.anchor_count? {\n              min: Math.min(...currentAchors.map((a) => a.price || 0)),\n              max: Math.max(...currentAchors.map((a) => a.price || 0)),\n              avg: currentAchors.reduce((sum, a) => sum + (a.price || 0), 0) / currentAchors.length\n            }:{\n              min:0,\n              max:0,\n              avg:0\n            },\n            similarity_range:result.anchor_count? {\n              min: Math.min(...currentAchors.map((a) => a.similarity || 0)),\n              max: Math.max(...currentAchors.map((a) => a.similarity || 0)),\n              avg: currentAchors.reduce((sum, a) => sum + (a.similarity || 0), 0) / currentAchors.length\n            }:{\n              min:0,\n              max:0,\n              avg:0\n            }\n          },\n          valuation: {\n            estimated_price_yuan: estimatedPrice,\n            confidence: result.confidence,\n            strategy: result.strategy || 'fair_value'\n          }\n        }\n\n        this.valuationLoading = false\n\n        // \n        if (result?.anchor_count > 0 && result?.anchors?.length > 0) {\n          // \n          this.anchorsLoading = true\n\n          try {\n            // anchor_eids\n            const anchorsResponse = await this.$api.role.getRoleApi({\n              page_size: 99,\n              eid_list: currentAchors.map(item => item.eid),\n              role_type: 'empty'\n            })\n\n            this.anchorsLoading = false\n            //\n            if (anchorsResponse.code === 200 && anchorsResponse.data?.data) {\n              const anchorsData = anchorsResponse.data.data\n              const parsedAnchors = anchorsData.map((item, index) => {\n                const roleInfo = new window.RoleInfoParser(item.large_equip_desc, { equip_level: item.equip_level })\n                item.RoleInfoParser = roleInfo\n                if (roleInfo.result) {\n                  item.roleInfo = roleInfo.result\n                }\n                item.similarity = currentAchors[index].similarity\n                item.features = currentAchors[index].features\n                return item\n              })\n\n              // \n              this.$set(this.similarData, 'anchors', parsedAnchors)\n            } else {\n              console.warn(':', anchorsResponse.message)\n            }\n          } catch (error) {\n            console.error(':', error)\n            // \n          }\n        }\n\n      } catch (error) {\n        console.error(':', error)\n        this.$notify.error({\n          title: '',\n          message: '',\n          duration: 3000\n        })\n        throw error\n      } finally {\n        // \n        this.valuationLoading = false\n        this.anchorsLoading = false\n      }\n    },\n    getStrategyName(strategy) {\n      const strategyNames = {\n        fair_value: '',\n        competitive: '',\n        premium: ''\n      }\n      return strategyNames[strategy] || strategy || '-'\n    }\n  }\n}\n</script>\n\n<style scoped>\n.similar-header {\n  padding-bottom: 12px;\n  border-bottom: 1px solid #ebeef5;\n}\n\n.similar-header h4 {\n  margin: 0;\n  color: #303133;\n  font-size: 16px;\n}\n\n.valuation-info {\n  margin: 12px 0;\n  padding: 12px;\n  background-color: #f8f9fa;\n  border-radius: 6px;\n  border-left: 4px solid #409eff;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.role-basic-info {\n  font-size: 14px;\n  color: #606266;\n}\n\n.valuation-main {\n  font-size: 14px;\n  display: flex;\n  align-items: center;\n  flex-wrap: wrap;\n  gap: 8px;\n}\n\n.valuation-label {\n  color: #606266;\n  font-weight: 500;\n}\n\n.valuation-price {\n  color: #e6a23c;\n  font-weight: 600;\n  font-size: 16px;\n}\n\n.valuation-strategy {\n  color: #909399;\n  font-size: 12px;\n}\n\n.valuation-details {\n  display: flex;\n  gap: 15px;\n  font-size: 12px;\n  color: #909399;\n  flex-wrap: wrap;\n}\n\n.confidence-display {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-weight: 500;\n}\n\n.confidence-display i {\n  font-size: 14px;\n}\n\n.confidence-level {\n  font-size: 11px;\n  opacity: 0.8;\n}\n\n.loading-info {\n  padding: 20px;\n}\n\n.stats {\n  display: flex;\n  gap: 20px;\n  font-size: 12px;\n  color: #909399;\n  flex-wrap: wrap;\n  margin-top: 10px;\n}\n\n/*  */\n.text-success {\n  color: #67c23a !important;\n}\n\n.text-primary {\n  color: #409eff !important;\n}\n\n.text-info {\n  color: #909399 !important;\n}\n\n.text-warning {\n  color: #e6a23c !important;\n}\n\n.text-danger {\n  color: #f56c6c !important;\n}\n\n/*  */\n.valuation-info.confidence-high {\n  border-left: 4px solid #67c23a;\n  background: linear-gradient(270deg, #f0f9ff 0%, #e1f3d8 100%);\n}\n\n.valuation-info.confidence-medium {\n  border-left: 4px solid #409eff;\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n\n.valuation-info.confidence-low {\n  border-left: 4px solid #909399;\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n\n.valuation-info.confidence-very-low {\n  border-left: 4px solid #e6a23c;\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n\n.valuation-info.confidence-extremely-low {\n  border-left: 4px solid #f56c6c;\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n</style>\n"],"sourceRoot":""}]);
