___CSS_LOADER_EXPORT___.push([module.id, "\n.login-modal[data-v-16190305] {\n  z-index: 10000;\n}\n.dialog-footer[data-v-16190305] {\n  text-align: right;\n}\n", "",{"version":3,"sources":["webpack://./src/chrome-extensions/LoginModal.vue"],"names":[],"mappings":";AA2RA;EACA,cAAA;AACA;AAEA;EACA,iBAAA;AACA","sourcesContent":["<template>\n  <el-dialog\n    :title=\"isRegister ? '' : ''\"\n    :visible.sync=\"visible\"\n    width=\"400px\"\n    :close-on-click-modal=\"true\"\n    :close-on-press-escape=\"true\"\n    :show-close=\"true\"\n    :modal=\"true\"\n    class=\"login-modal\"\n  >\n    <!--  -->\n    <el-form \n      v-if=\"!isRegister\"\n      :model=\"loginForm\" \n      :rules=\"rules\" \n      ref=\"loginForm\" \n      label-width=\"80px\"\n    >\n      <el-form-item label=\"\" prop=\"username\">\n        <el-input \n          v-model=\"loginForm.username\" \n          placeholder=\"\" \n          autocomplete=\"off\"\n        ></el-input>\n      </el-form-item>\n      <el-form-item label=\"\" prop=\"password\">\n        <el-input\n          v-model=\"loginForm.password\"\n          type=\"password\"\n          placeholder=\"\"\n          autocomplete=\"off\"\n          @keyup.enter.native=\"handleLogin\"\n        ></el-input>\n      </el-form-item>\n    </el-form>\n    \n    <!--  -->\n    <el-form \n      v-else\n      :model=\"registerForm\" \n      :rules=\"registerRules\" \n      ref=\"registerForm\" \n      label-width=\"80px\"\n    >\n      <el-form-item label=\"\" prop=\"username\">\n        <el-input \n          v-model=\"registerForm.username\" \n          placeholder=\"\" \n          autocomplete=\"off\"\n        ></el-input>\n      </el-form-item>\n      <el-form-item label=\"\" prop=\"password\">\n        <el-input\n          v-model=\"registerForm.password\"\n          type=\"password\"\n          placeholder=\"\"\n          autocomplete=\"off\"\n          @keyup.enter.native=\"handleRegister\"\n        ></el-input>\n      </el-form-item>\n      <el-form-item label=\"\" prop=\"confirmPassword\">\n        <el-input\n          v-model=\"registerForm.confirmPassword\"\n          type=\"password\"\n          placeholder=\"\"\n          autocomplete=\"off\"\n          @keyup.enter.native=\"handleRegister\"\n        ></el-input>\n      </el-form-item>\n    </el-form>\n    \n    <div slot=\"footer\" class=\"dialog-footer\">\n      <el-button @click=\"toggleMode\">{{ isRegister ? '' : '' }}</el-button>\n      <el-button \n        v-if=\"!isRegister\"\n        type=\"primary\" \n        @click=\"handleLogin\" \n        :loading=\"loading\"\n      >\n        \n      </el-button>\n      <el-button \n        v-else\n        type=\"primary\" \n        @click=\"handleRegister\" \n        :loading=\"loading\"\n      >\n        \n      </el-button>\n    </div>\n  </el-dialog>\n</template>\n\n<script>\nimport { api } from '@/utils/request'\n\nexport default {\n  name: 'LoginModal',\n  props: {\n    value: {\n      type: Boolean,\n      default: false\n    }\n  },\n  data() {\n    // \n    const validateConfirmPassword = (rule, value, callback) => {\n      if (value === '') {\n        callback(new Error(''))\n      } else if (value !== this.registerForm.password) {\n        callback(new Error(''))\n      } else {\n        callback()\n      }\n    }\n    \n    return {\n      visible: this.value,\n      loading: false,\n      isRegister: false, // \n      loginForm: {\n        username: '',\n        password: ''\n      },\n      registerForm: {\n        username: '',\n        password: '',\n        confirmPassword: ''\n      },\n      rules: {\n        username: [\n          { required: true, message: '', trigger: 'blur' },\n          { pattern: /^1[3-9]\\d{9}$/, message: '', trigger: 'blur' }\n        ],\n        password: [\n          { required: true, message: '', trigger: 'blur' },\n          { min: 6, message: '6', trigger: 'blur' }\n        ]\n      },\n      registerRules: {\n        username: [\n          { required: true, message: '', trigger: 'blur' },\n          { pattern: /^1[3-9]\\d{9}$/, message: '', trigger: 'blur' }\n        ],\n        password: [\n          { required: true, message: '', trigger: 'blur' },\n          { min: 6, message: '6', trigger: 'blur' }\n        ],\n        confirmPassword: [\n          { required: true, message: '', trigger: 'blur' },\n          { validator: validateConfirmPassword, trigger: 'blur' }\n        ]\n      }\n    }\n  },\n  watch: {\n    value(newVal) {\n      this.visible = newVal\n    },\n    visible(newVal) {\n      this.$emit('input', newVal)\n    }\n  },\n  methods: {\n    async handleLogin() {\n      this.$refs.loginForm.validate(async (valid) => {\n        if (!valid) return\n        \n        this.loading = true\n        try {\n          const response = await api.post('/auth/login', {\n            username: this.loginForm.username,\n            password: this.loginForm.password\n          })\n          \n          if (response.code === 200) {\n            // \n            if (response.message && response.message.includes('')) {\n              this.$message.info(response.message)\n            } else if (response.data && response.data.token) {\n              // tokenchrome.storage\n              await this.saveToken(response.data.token, response.data.user)\n              \n              this.$message.success('')\n              this.$emit('login-success', response.data)\n              this.visible = false\n            } else {\n              // \n              this.$message.info(response.message || '')\n            }\n          } else {\n            this.$message.error(response.message || '')\n          }\n        } catch (error) {\n          console.error(':', error)\n          this.$message.error('')\n        } finally {\n          this.loading = false\n        }\n      })\n    },\n    \n    toggleMode() {\n      this.isRegister = !this.isRegister\n      //  DOM \n      this.$nextTick(() => {\n        if (this.$refs.loginForm) {\n          this.$refs.loginForm.resetFields()\n        }\n        if (this.$refs.registerForm) {\n          this.$refs.registerForm.resetFields()\n        }\n      })\n    },\n    \n    async handleRegister() {\n      // \n      if (!this.$refs.registerForm) {\n        console.error('')\n        this.$message.error('')\n        return\n      }\n      \n      this.$refs.registerForm.validate(async (valid) => {\n        if (!valid) {\n          console.log('')\n          this.$message.warning('')\n          return\n        }\n        \n        console.log(':', this.registerForm.username)\n        this.loading = true\n        try {\n          const response = await api.post('/auth/register', {\n            username: this.registerForm.username,\n            password: this.registerForm.password\n          })\n          \n          console.log(':', response)\n          \n          if (response.code === 200) {\n            this.$message.success(response.message || '')\n            // \n            const username = this.registerForm.username\n            // \n            this.isRegister = false\n            //  DOM \n            this.$nextTick(() => {\n              this.loginForm.username = username\n              if (this.$refs.registerForm) {\n                this.$refs.registerForm.resetFields()\n              }\n            })\n          } else {\n            this.$message.error(response.message || '')\n          }\n        } catch (error) {\n          console.error(':', error)\n          // \n          const errorMessage = error.response?.data?.message || error.message || ''\n          this.$message.error(errorMessage)\n        } finally {\n          this.loading = false\n        }\n      })\n    },\n    \n    async saveToken(token, user) {\n      return new Promise((resolve) => {\n        chrome.storage.local.set({\n          auth_token: token,\n          user_info: user\n        }, () => {\n          resolve()\n        })\n      })\n    }\n  }\n}\n</script>\n\n<style scoped>\n.login-modal {\n  z-index: 10000;\n}\n\n.dialog-footer {\n  text-align: right;\n}\n</style>\n\n"],"sourceRoot":""}]);
