f9ff 0%, #e1f3d8 100%);\n}\n.valuation-info.confidence-medium[data-v-09f59ad2] {\n  border-left: 4px solid #409eff;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n.valuation-info.confidence-low[data-v-09f59ad2] {\n  border-left: 4px solid #909399;\n  /*  -  */\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n.valuation-info.confidence-very-low[data-v-09f59ad2] {\n  border-left: 4px solid #e6a23c;\n  /*  -  */\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n.valuation-info.confidence-extremely-low[data-v-09f59ad2] {\n  border-left: 4px solid #f56c6c;\n  /*  -  */\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n", "",{"version":3,"sources":["webpack://./src/components/PetValuation.vue"],"names":[],"mappings":";AA8NA;EACA,mBAAA;EACA,yBAAA;EACA,kBAAA;EACA,aAAA;EACA,mBAAA;AACA;AAEA;EACA,aAAA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,cAAA;EACA,iBAAA;AACA;AAEA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,cAAA;EACA,eAAA;EACA,mBAAA;EACA,gBAAA;AACA;AAEA;EACA,iBAAA;EACA,gBAAA;EACA,kBAAA;EACA,eAAA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;EACA,cAAA;AACA;AAEA;EACA,mBAAA;EACA,cAAA;AACA;AAEA;EACA,mBAAA;EACA,cAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,mBAAA;AACA;;AAEA,YAAA;AACA;EACA,aAAA;EACA,mBAAA;EACA,QAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,iBAAA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,8BAAA;AACA;;AAEA,UAAA;AACA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;;AAEA,eAAA;AACA;EACA,8BAAA;EACA,cAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA","sourcesContent":["<template>\n  <div class=\"valuation-info\" :class=\"confidenceClass\">\n    <el-row type=\"flex\" align=\"middle\" justify=\"space-between\">\n      <el-row type=\"flex\" align=\"middle\">\n        <PetImage :pet=\"targetPet.petData\" trigger=\"hover\" :equipFaceImg=\"targetPet.equip_face_img\" />\n        <span v-html=\"formatFullPrice(targetPet)\" style=\"margin-left: 10px\"></span>\n      </el-row>\n      <!--  -->\n      <div style=\"width: 170px;flex-shrink: 0;\">\n        <el-button type=\"primary\" @click=\"$emit('refresh')\" size=\"mini\" style=\"margin-right: 5px;\"></el-button>\n        <SimilarGetMore :target-equipment=\"targetPet\" type=\"pet\" />\n      </div>\n    </el-row>\n    <div class=\"valuation-main\">\n      <span class=\"valuation-label\">:</span>\n      <span class=\"valuation-price\">{{ valuation ? (valuation.estimated_price_yuan + valuation.equip_estimated_price / 100\n      ).toFixed(2) + '' : '-' }} </span>\n      <span class=\"valuation-strategy\">({{ valuation ? getStrategyName(valuation.strategy) : '-' }})</span>\n\n      <!--  -->\n      <span v-if=\"priceRatio\" class=\"price-ratio\" :class=\"priceRatioClass\">\n        <el-tag :type=\"priceRatioTagType\" disable-transitions>\n          {{ priceRatioText }}\n        </el-tag>\n      </span>\n    </div>\n    <div class=\"valuation-equip\">\n      <el-tag>:{{ valuation.estimated_price_yuan }}</el-tag> <el-divider direction=\"vertical\" />\n      <el-tag type=\"success\">:{{ valuation.equip_estimated_price / 100 }}</el-tag>\n    </div>\n    <div class=\"valuation-details\">\n      <span class=\"confidence-display\" :class=\"confidenceTextClass\">\n        <i :class=\"confidenceIcon\"></i>\n        : {{ valuation ? (valuation.confidence * 100).toFixed(1) + '%' : '-' }}\n        <span class=\"confidence-level\">{{ confidenceLevel }}</span>\n      </span>\n      <span>{{ valuation ? valuation.anchor_count + '' : '-' }}</span>\n      <span v-if=\"priceRatio\">: {{ (priceRatio * 100).toFixed(1) }}%</span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport PetImage from './PetImage.vue'\nimport { petMixin } from '@/utils/mixins/petMixin'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\nimport SimilarGetMore from './SimilarGetMore.vue'\nexport default {\n  name: 'PetValuation',\n  components: {\n    PetImage,\n    SimilarGetMore\n  },\n  mixins: [petMixin, commonMixin],\n  props: {\n    valuation: {\n      type: Object,\n      default: null\n    },\n    targetPet: {\n      type: Object,\n      required: true\n    }\n  },\n  computed: {\n    // \n    priceRatio() {\n      if (!this.valuation || !this.valuation.estimated_price_yuan || !this.targetPet.price) {\n        return null\n      }\n\n      const estimatedPrice = parseFloat(this.valuation.estimated_price)\n      const sellingPrice = parseFloat(this.targetPet.price)\n      if (sellingPrice === 0) return null\n\n      return estimatedPrice / sellingPrice\n    },\n\n    // \n    priceRatioText() {\n      if (!this.priceRatio) return ''\n      const ratio = this.priceRatio\n      const deviation = Math.abs(ratio - 1) * 100\n      if (deviation < 5) {\n        return `${deviation.toFixed(1)}%`\n      } else if (deviation < 10) {\n        return `${deviation.toFixed(1)}%`\n      } else if (deviation < 20) {\n        return `${deviation.toFixed(1)}%`\n      } else if (ratio > 1) {\n        return `+${((ratio - 1) * 100).toFixed(1)}%`\n      } else {\n        return `-${((1 - ratio) * 100).toFixed(1)}%`\n      }\n    },\n\n    // \n    priceRatioClass() {\n      if (!this.priceRatio) return ''\n\n      const ratio = this.priceRatio\n      if (ratio > 1.2) {\n        return 'ratio-low' // \n      } else if (ratio < 0.8) {\n        return 'ratio-high' // \n      } else {\n        return 'ratio-normal'\n      }\n    },\n\n    priceRatioTagType() {\n      if (!this.priceRatio) return ''\n      const ratio = this.priceRatio\n      const deviation = Math.abs(ratio - 1) * 100\n      if (deviation < 5) {\n        return 'success'\n      } else if (deviation < 10) {\n        return 'info'\n      } else if (deviation < 20) {\n        return 'warning'\n      } else {\n        return 'danger'\n      }\n    },\n\n    // CSS\n    confidenceClass() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return 'confidence-extremely-low'\n      }\n\n      const confidence = this.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return 'confidence-high'        // >= 80%:  ()\n      } else if (confidence >= 0.6) {\n        return 'confidence-medium'      // 60-79%:  ()\n      } else if (confidence >= 0.4) {\n        return 'confidence-low'         // 40-59%:  ()\n      } else if (confidence >= 0.2) {\n        return 'confidence-very-low'    // 20-39%:  ()\n      } else {\n        return 'confidence-extremely-low' // < 20%:  ()\n      }\n    },\n\n    // \n    confidenceTextClass() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return 'text-danger'\n      }\n\n      const confidence = this.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return 'text-success'\n      } else if (confidence >= 0.6) {\n        return 'text-primary'\n      } else if (confidence >= 0.4) {\n        return 'text-info'\n      } else if (confidence >= 0.2) {\n        return 'text-warning'\n      } else {\n        return 'text-danger'\n      }\n    },\n\n    // \n    confidenceIcon() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return 'el-icon-warning'\n      }\n\n      const confidence = this.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return 'el-icon-success'\n      } else if (confidence >= 0.6) {\n        return 'el-icon-info'\n      } else if (confidence >= 0.4) {\n        return 'el-icon-warning'\n      } else {\n        return 'el-icon-error'\n      }\n    },\n\n    // \n    confidenceLevel() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return ''\n      }\n\n      const confidence = this.valuation.confidence\n\n      if (confidence >= 0.8) {\n        return ''\n      } else if (confidence >= 0.6) {\n        return ''\n      } else if (confidence >= 0.4) {\n        return ''\n      } else if (confidence >= 0.2) {\n        return ''\n      } else {\n        return ''\n      }\n    }\n  },\n  methods: {\n    // \n    getStrategyName(strategy) {\n      const strategyMap = {\n        'fair_value': '',\n        'market_price': '',\n        'weighted_average': ''\n      }\n      return strategyMap[strategy] || strategy\n    },\n  }\n}\n</script>\n\n<style scoped>\n.valuation-info {\n  background: #f8f9fa;\n  border: 1px solid #e9ecef;\n  border-radius: 6px;\n  padding: 12px;\n  margin-bottom: 16px;\n}\n\n.valuation-main {\n  margin: 8px 0;\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.valuation-label {\n  color: #606266;\n  margin-right: 8px;\n}\n\n.valuation-price {\n  color: #e6a23c;\n  font-size: 18px;\n}\n\n.valuation-strategy {\n  color: #909399;\n  font-size: 12px;\n  font-weight: normal;\n  margin-left: 8px;\n}\n\n.price-ratio {\n  margin-left: 12px;\n  padding: 2px 6px;\n  border-radius: 3px;\n  font-size: 12px;\n  font-weight: normal;\n}\n\n.ratio-low {\n  background: #f0f9ff;\n  color: #1890ff;\n}\n\n.ratio-high {\n  background: #fff2f0;\n  color: #ff4d4f;\n}\n\n.ratio-normal {\n  background: #f6ffed;\n  color: #52c41a;\n}\n\n.valuation-details {\n  display: flex;\n  gap: 16px;\n  font-size: 12px;\n  color: #909399;\n  margin-top: 8px;\n}\n\n.valuation-details span {\n  white-space: nowrap;\n}\n\n/*  */\n.confidence-display {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n}\n\n.confidence-display i {\n  font-size: 14px;\n}\n\n.confidence-level {\n  font-weight: bold;\n  font-size: 11px;\n  padding: 1px 4px;\n  border-radius: 2px;\n  background: rgba(0, 0, 0, 0.1);\n}\n\n/*  */\n.text-success {\n  color: #67c23a !important;\n}\n\n.text-primary {\n  color: #409eff !important;\n}\n\n.text-info {\n  color: #909399 !important;\n}\n\n.text-warning {\n  color: #e6a23c !important;\n}\n\n.text-danger {\n  color: #f56c6c !important;\n}\n\n/*  */\n.valuation-info.confidence-high {\n  border-left: 4px solid #67c23a;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f9ff 0%, #e1f3d8 100%);\n}\n\n.valuation-info.confidence-medium {\n  border-left: 4px solid #409eff;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n\n.valuation-info.confidence-low {\n  border-left: 4px solid #909399;\n  /*  -  */\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n\n.valuation-info.confidence-very-low {\n  border-left: 4px solid #e6a23c;\n  /*  -  */\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n\n.valuation-info.confidence-extremely-low {\n  border-left: 4px solid #f56c6c;\n  /*  -  */\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n</style>"],"sourceRoot":""}]);
