___CSS_LOADER_EXPORT___.push([module.id, "\n.valuation-info[data-v-b3167e7a] {\n  margin: 12px 0;\n  padding: 12px;\n  background-color: #f8f9fa;\n  border-radius: 6px;\n  border-left: 4px solid #409eff;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n.valuation-main[data-v-b3167e7a] {\n  font-size: 14px;\n  display: flex;\n  align-items: center;\n  flex-wrap: wrap;\n  gap: 8px;\n}\n.valuation-label[data-v-b3167e7a] {\n  color: #606266;\n  font-weight: 500;\n}\n.valuation-price[data-v-b3167e7a] {\n  color: #e6a23c;\n  font-weight: 600;\n  font-size: 16px;\n}\n.valuation-strategy[data-v-b3167e7a] {\n  color: #909399;\n  font-size: 12px;\n}\n.valuation-details[data-v-b3167e7a] {\n  display: flex;\n  gap: 15px;\n  font-size: 12px;\n  color: #909399;\n  flex-wrap: wrap;\n}\n.confidence-display[data-v-b3167e7a] {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-weight: 500;\n}\n.confidence-display i[data-v-b3167e7a] {\n  font-size: 14px;\n}\n.confidence-level[data-v-b3167e7a] {\n  font-size: 11px;\n  opacity: 0.8;\n}\n\n/*  */\n.text-success[data-v-b3167e7a] {\n  color: #67c23a !important;\n}\n.text-primary[data-v-b3167e7a] {\n  color: #409eff !important;\n}\n.text-info[data-v-b3167e7a] {\n  color: #909399 !important;\n}\n.text-warning[data-v-b3167e7a] {\n  color: #e6a23c !important;\n}\n.text-danger[data-v-b3167e7a] {\n  color: #f56c6c !important;\n}\n/*  */\n.valuation-info.confidence-high[data-v-b3167e7a] {\n  border-left: 4px solid #67c23a;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f9ff 0%, #e1f3d8 100%);\n}\n.valuation-info.confidence-medium[data-v-b3167e7a] {\n  border-left: 4px solid #409eff;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n.valuation-info.confidence-low[data-v-b3167e7a] {\n  border-left: 4px solid #909399;\n  /*  -  */\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n.valuation-info.confidence-very-low[data-v-b3167e7a] {\n  border-left: 4px solid #e6a23c;\n  /*  -  */\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n.valuation-info.confidence-extremely-low[data-v-b3167e7a] {\n  border-left: 4px solid #f56c6c;\n  /*  -  */\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n", "",{"version":3,"sources":["webpack://./src/components/EquipmentValuation.vue"],"names":[],"mappings":";AAgRA;EACA,cAAA;EACA,aAAA;EACA,yBAAA;EACA,kBAAA;EACA,8BAAA;EACA,aAAA;EACA,sBAAA;EACA,QAAA;AACA;AAEA;EACA,eAAA;EACA,aAAA;EACA,mBAAA;EACA,eAAA;EACA,QAAA;AACA;AAEA;EACA,cAAA;EACA,gBAAA;AACA;AAEA;EACA,cAAA;EACA,gBAAA;EACA,eAAA;AACA;AAEA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,SAAA;EACA,eAAA;EACA,cAAA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,QAAA;EACA,gBAAA;AACA;AAEA;EACA,eAAA;AACA;AAEA;EACA,eAAA;EACA,YAAA;AACA;;AAEA,YAAA;AACA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AAEA;EACA,yBAAA;AACA;AACA,eAAA;AACA;EACA,8BAAA;EACA,cAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA;AAEA;EACA,8BAAA;EACA,eAAA;EACA,6DAAA;AACA","sourcesContent":["<template>\n  <div class=\"valuation-info\" :class=\"confidenceClass\">\n    <el-row type=\"flex\" align=\"middle\" justify=\"space-between\">\n      <el-row type=\"flex\" align=\"middle\">\n        <EquipmentImage :equipment=\"targetEquipment\" width=\"50px\" height=\"50px\" placement=\"left\" />\n        <span v-if=\"targetEquipment.price\" v-html=\"formatFullPrice(targetEquipment)\" style=\"margin-left: 10px\"></span>\n        <div v-if=\"isWeapon\" style=\"margin-left: 10px\">\n          <div style=\"margin-bottom: 5px\"> <el-tag type=\"success\">+{{ valuation.feature.addon_total }}{{\n            addon_total_standards?.[valuation.feature.equip_level] }}</el-tag>\n            <el-divider direction=\"vertical\" />\n            <el-tag type=\"primary\">{{ valuation.feature.all_damage }}/{{\n              parseInt(all_damage_standards?.[valuation.feature.equip_level][1]\n                + (25 * valuation.feature.equip_level / 30)) }}</el-tag>\n          </div>\n          <div>\n            <el-tag type=\"danger\">{{ valuation.feature.init_damage_raw }}{{\n              init_damage_raw_standards?.[valuation.feature.equip_level] }}</el-tag>\n            <el-divider direction=\"vertical\" />\n            <el-tag type=\"danger\">{{ valuation.feature.init_damage }}{{\n              all_damage_standards?.[valuation.feature.equip_level] }}</el-tag>\n          </div>\n        </div>\n      </el-row>\n      <!--  -->\n      <div style=\"width: 170px;flex-shrink: 0;\">\n        <el-button type=\"primary\" @click=\"$emit('refresh')\" size=\"mini\" style=\"margin-right: 5px;\"></el-button>\n        <SimilarGetMore :target-equipment=\"targetEquipment\" />\n      </div>\n    </el-row>\n    <div class=\"valuation-main\">\n      <span class=\"valuation-label\">:</span>\n      <span class=\"valuation-price\">{{ valuation?.estimated_price_yuan || '-' }}</span>\n      <span class=\"valuation-strategy\">({{ valuation ? getStrategyName(valuation.strategy) : '-' }})</span>\n      <el-link type=\"danger\" @click.native=\"markAsAbnormal\" size=\"mini\"></el-link>\n\n      <!--  -->\n      <span v-if=\"priceRatio\" class=\"price-ratio\">\n        <el-tag :type=\"priceRatioTagType\" disable-transitions>\n          {{ priceRatioText }}\n        </el-tag>\n      </span>\n    </div>\n    <div class=\"valuation-details\">\n      <span class=\"confidence-display\" :class=\"confidenceTextClass\">\n        <i :class=\"confidenceIcon\"></i>\n        : {{ valuation ? (valuation.confidence * 100).toFixed(1) + '%' : '-' }}\n        <span class=\"confidence-level\">{{ confidenceLevel }}</span>\n      </span>\n      <span>{{ valuation.anchor_count }}</span>\n      <span v-if=\"priceRatio\">: {{ (priceRatio * 100).toFixed(1) }}%</span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport EquipmentImage from './EquipmentImage/EquipmentImage.vue'\nimport { equipmentMixin } from '@/utils/mixins/equipmentMixin'\nimport { commonMixin } from '@/utils/mixins/commonMixin'\nimport SimilarGetMore from './SimilarGetMore.vue'\n\nexport default {\n  name: 'EquipmentValuation',\n  components: {\n    EquipmentImage,\n    SimilarGetMore\n  },\n  mixins: [equipmentMixin, commonMixin],\n  props: {\n    valuation: {\n      type: Object,\n      default: null\n    },\n    targetEquipment: {\n      type: Object,\n      required: true\n    }\n  },\n  data() {\n    return {\n      weaponConfig: null,\n      addon_total_standards: null,\n      all_damage_standards: null,\n      init_damage_raw_standards: null,\n    }\n  },\n  computed: {\n    isWeapon() {\n      return window.is_weapon_equip(this.valuation?.feature?.kindid)\n    },\n    // \n    priceRatio() {\n      if (!this.valuation || !this.valuation.estimated_price_yuan || !this.targetEquipment.price) {\n        return null\n      }\n\n      const estimatedPrice = parseFloat(this.valuation.estimated_price_yuan)\n      const sellingPrice = parseFloat(this.targetEquipment.price) / 100 // \n\n      if (sellingPrice === 0) return null\n\n      return estimatedPrice / sellingPrice\n    },\n    priceRatioTagType() {\n      if (!this.priceRatio) return ''\n      const ratio = this.priceRatio\n      const deviation = Math.abs(ratio - 1) * 100\n      if (deviation < 5) {\n        return 'success'\n      } else if (deviation < 10) {\n        return 'info'\n      } else if (deviation < 20) {\n        return 'warning'\n      } else {\n        return 'danger'\n      }\n    },\n    priceRatioText() {\n      if (!this.priceRatio) return ''\n      const ratio = this.priceRatio\n      const deviation = Math.abs(ratio - 1) * 100\n      if (deviation < 5) {\n        return `${deviation.toFixed(1)}%`\n      } else if (deviation < 10) {\n        return `${deviation.toFixed(1)}%`\n      } else if (deviation < 20) {\n        return `${deviation.toFixed(1)}%`\n      } else if (ratio > 1) {\n        return `+${((ratio - 1) * 100).toFixed(1)}%`\n      } else {\n        return `-${((1 - ratio) * 100).toFixed(1)}%`\n      }\n    },\n    // CSS\n    confidenceClass() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return 'confidence-extremely-low'\n      }\n      \n      const confidence = this.valuation.confidence\n      \n      if (confidence >= 0.8) {\n        return 'confidence-high'        // >= 80%:  ()\n      } else if (confidence >= 0.6) {\n        return 'confidence-medium'      // 60-79%:  ()\n      } else if (confidence >= 0.4) {\n        return 'confidence-low'         // 40-59%:  ()\n      } else if (confidence >= 0.2) {\n        return 'confidence-very-low'    // 20-39%:  ()\n      } else {\n        return 'confidence-extremely-low' // < 20%:  ()\n      }\n    },\n    // \n    confidenceTextClass() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return 'text-danger'\n      }\n      \n      const confidence = this.valuation.confidence\n      \n      if (confidence >= 0.8) {\n        return 'text-success'\n      } else if (confidence >= 0.6) {\n        return 'text-primary'\n      } else if (confidence >= 0.4) {\n        return 'text-info'\n      } else if (confidence >= 0.2) {\n        return 'text-warning'\n      } else {\n        return 'text-danger'\n      }\n    },\n    // \n    confidenceIcon() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return 'el-icon-warning'\n      }\n      \n      const confidence = this.valuation.confidence\n      \n      if (confidence >= 0.8) {\n        return 'el-icon-success'\n      } else if (confidence >= 0.6) {\n        return 'el-icon-info'\n      } else if (confidence >= 0.4) {\n        return 'el-icon-question'\n      } else if (confidence >= 0.2) {\n        return 'el-icon-warning'\n      } else {\n        return 'el-icon-error'\n      }\n    },\n    // \n    confidenceLevel() {\n      if (!this.valuation || !this.valuation.confidence) {\n        return '()'\n      }\n      \n      const confidence = this.valuation.confidence\n      \n      if (confidence >= 0.8) {\n        return '()'\n      } else if (confidence >= 0.6) {\n        return '()'\n      } else if (confidence >= 0.4) {\n        return '()'\n      } else if (confidence >= 0.2) {\n        return '()'\n      } else {\n        return '()'\n      }\n    },\n  },\n  methods: {\n    getWeaponConfig() {\n      this.$api.equipment.getWeaponConfig().then(res => {\n        if (res.code === 200) {\n          this.weaponConfig = res.data\n          this.addon_total_standards = res.data.addon_total_standards\n          this.all_damage_standards = res.data.all_damage_standards\n          this.init_damage_raw_standards = res.data.init_damage_raw_standards\n        }\n      })\n    },\n    async markAsAbnormal() {\n      try {\n        // API\n        const response = await this.$api.equipment.markEquipmentAsAbnormal({\n          equipment_data: this.targetEquipment,\n          reason: '',\n          notes: ''\n        })\n\n        if (response.code === 200) {\n          this.$notify.success({\n            title: '',\n            message: ''\n          })\n          // \n          this.$emit('abnormal-marked', this.targetEquipment)\n        } else {\n          this.$notify.error({\n            title: '',\n            message: response.message || ''\n          })\n        }\n      } catch (error) {\n        console.error(':', error)\n        this.$notify.error({\n          title: '',\n          message: ''\n        })\n      }\n    },\n    getStrategyName(strategy) {\n      const strategyNames = {\n        fair_value: '',\n        competitive: '',\n        premium: ''\n      }\n      return strategyNames[strategy] || strategy\n    },\n  },\n  mounted() {\n    if (this.isWeapon) {\n      this.getWeaponConfig()\n    }\n  }\n}\n</script>\n\n<style scoped>\n.valuation-info {\n  margin: 12px 0;\n  padding: 12px;\n  background-color: #f8f9fa;\n  border-radius: 6px;\n  border-left: 4px solid #409eff;\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.valuation-main {\n  font-size: 14px;\n  display: flex;\n  align-items: center;\n  flex-wrap: wrap;\n  gap: 8px;\n}\n\n.valuation-label {\n  color: #606266;\n  font-weight: 500;\n}\n\n.valuation-price {\n  color: #e6a23c;\n  font-weight: 600;\n  font-size: 16px;\n}\n\n.valuation-strategy {\n  color: #909399;\n  font-size: 12px;\n}\n\n.valuation-details {\n  display: flex;\n  gap: 15px;\n  font-size: 12px;\n  color: #909399;\n  flex-wrap: wrap;\n}\n\n.confidence-display {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  font-weight: 500;\n}\n\n.confidence-display i {\n  font-size: 14px;\n}\n\n.confidence-level {\n  font-size: 11px;\n  opacity: 0.8;\n}\n\n/*  */\n.text-success {\n  color: #67c23a !important;\n}\n\n.text-primary {\n  color: #409eff !important;\n}\n\n.text-info {\n  color: #909399 !important;\n}\n\n.text-warning {\n  color: #e6a23c !important;\n}\n\n.text-danger {\n  color: #f56c6c !important;\n}\n/*  */\n.valuation-info.confidence-high {\n  border-left: 4px solid #67c23a;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f9ff 0%, #e1f3d8 100%);\n}\n\n.valuation-info.confidence-medium {\n  border-left: 4px solid #409eff;\n  /*  -  */\n  background: linear-gradient(270deg, #f0f8ff 0%, #e1f5fe 100%);\n}\n\n.valuation-info.confidence-low {\n  border-left: 4px solid #909399;\n  /*  -  */\n  background: linear-gradient(270deg, #f8f9fa 0%, #e9ecef 100%);\n}\n\n.valuation-info.confidence-very-low {\n  border-left: 4px solid #e6a23c;\n  /*  -  */\n  background: linear-gradient(270deg, #fdf6ec 0%, #fdf2e9 100%);\n}\n\n.valuation-info.confidence-extremely-low {\n  border-left: 4px solid #f56c6c;\n  /*  -  */\n  background: linear-gradient(270deg, #fef0f0 0%, #fde2e2 100%);\n}\n</style>\n"],"sourceRoot":""}]);
